 .react-resizable {
    position: relative;
    background-clip: padding-box;
  }
  
 .react-resizable-handle {
    position: absolute;
    width: 10px;
    height: 100%;
    bottom: 0;
    right: -5px;
    cursor: col-resize;
    z-index: 1;
  }
  .substr {
    overflow: hidden;
    white-space: nowrap;
    text-overflow:ellipsis;
    cursor:pointer;
  }